import React, { useLayoutEffect, useState } from "react";

import "../../css/member.scss";
import { Link, useNavigate } from "react-router-dom";

import { initData } from "../func/mem_fn";

function Member(props) {
  const goNav = useNavigate();
  // 1. 
  const [userId, setUserId] = useState("");
  // 2. 
  const [pwd, setPwd] = useState("");
  // 3. 
  const [chkPwd, setChkPwd] = useState("");
  // 4. 
  const [userName, setUserName] = useState("");
  // 5. 
  const [email, setEmail] = useState("");

  //  
  const [userIdError, setUserIdError] = useState(false);
  // 2. 
  const [pwdError, setPwdError] = useState(false);
  // 3. 
  const [chkPwdError, setChkPwdError] = useState(false);
  // 4. 
  const [userNameError, setUserNameError] = useState(false);
  // 5. 
  const [emailError, setEmailError] = useState(false);

  // [    ] ////
  const msgId = [
    // 1.  6  
    "User ID must contain a minimum of 6 characters",
    // 2.   
    "This ID is already in use!",
    // 3.  
    "That's a great ID!",
  ];

  // [    ]
  const msgEtc = {
    // 
    pwd: "6 to 15 digits in the form of special characters, characters, and numbers",
    // 
    confPwd: "Password verification does not match",
    // 
    req: "This is a required entry",
    // 
    email: "Please enter a valid email format",
  }; ///// msgEtc ///////

  // [3]   :  msgId[0]
  // ->   
  const [idMsg, setIdMsg] = useState(msgId[0]);

  const changeUserId = (e) => {
    let val = e.target.value;

    setUserId(val);

    const valid = /^[A-Za-z0-9+]{6,15}$/;

    if (valid.test(val)) {
      // console.log("");

      initData();

      let memData = localStorage.getItem("mem-data");
      memData = JSON.parse(memData);

      let isTrue = memData.some((v) => v.uid == val);

      if (isTrue) {
        //  
        setIdMsg(msgId[1]);
        // 
        setUserIdError(true);
      } else {
        //  
        setUserIdError(false);
      }

      // console.log("id", isTrue);
      memData.forEach((v) => console.log(v));
    } else {
      // console.log("");
      //   
      setIdMsg(msgId[0]);
      setUserIdError(true);
    }
  }; // changeUserId
  //   

  const changePwd = (e) => {
    let val = e.target.value;

    const valpwd = /^.*(?=^.{5,15}$)(?=.*\d)(?=.*[a-zA-Z])(?=.*[!@#$%^&+=]).*$/;

    if (valpwd.test(val)) setPwdError(false);
    else setPwdError(true);

    setPwd(val);
  }; // changePwd

  // 3.    ///////////
  const changeChkPwd = (e) => {
    //  
    let val = e.target.value;

    // 1.    
    if (pwd === val) setChkPwdError(false);
    else setChkPwdError(true);

    // 2.  
    setChkPwd(val);
  }; ///////// changeChkPwd  //////////

  // 4.    ///////////
  const changeUserName = (e) => {
    //  
    let val = e.target.value;

    // 1. 
    if (val !== "") setUserNameError(false);
    else setUserNameError(true);

    // 2.  
    setUserName(val);
  }; ///////// changeUserName  //////////

  // 5.    ///////////
  const changeEmail = (e) => {
    //  
    let val = e.target.value;

    // 1.   (  !)
    const valid =
      /^(([^<>()\[\].,;:\s@"]+(\.[^<>()\[\].,;:\s@"]+)*)|(".+"))@(([^<>()[\].,;:\s@"]+\.)+[^<>()[\].,;:\s@"]{2,})$/i;

    // 2.   : e.target ->   
    // console.log(val);

    // 3.    
    if (valid.test(val)) setEmailError(false);
    else setEmailError(true);

    // 4.  
    setEmail(val);
  }; ///////// changeEmail  //////////

  // [    ] ///////////
  const totalValid = () => {
    // 1.     !
    if (!userId) setUserIdError(true);
    if (!pwd) setPwdError(true);
    if (!chkPwd) setChkPwdError(true);
    if (!userName) setUserNameError(true);
    if (!email) setEmailError(true);

    // 2.  true,  false 
    //  :  +   false
    if (
      userId &&
      pwd &&
      chkPwd &&
      userName &&
      email &&
      !userIdError &&
      !pwdError &&
      !chkPwdError &&
      !userNameError &&
      !emailError
    )
      return true;
    //  false false !
    else return false;
  }; /////////// totalValid  ///////////

  //  ///

  const onSubmit = (e) => {
    e.preventDefault();

    console.log(" ", totalValid());

    if (totalValid()) {
      console.log(" ");

      initData();

      let memData = localStorage.getItem("mem-data");

      memData = JSON.parse(memData);

      let temp = memData.map((v) => v.idx);

      let newData = {
        idx: Math.max(...temp) + 1,
        uid: userId,
        pwd: pwd,
        uname: userName,
        email: email,
      };

      memData.push(newData);

      localStorage.setItem("mem-data", JSON.stringify(memData));

      goNav("/login");
    } else {
      alert("Change your input");
    }
  }; // on Submit

  return (
    <div id="member-area">
      <section className="member-area">
        <div className="member-wrap">
          <h2>Create Account</h2>
          <div className="signtxt">
            <p>Already have an account? &nbsp;</p>
          </div>
          <div className="signtxt">
            <p className="signup">
              <Link to="/login">Sign in here.</Link>
            </p>
          </div>
          <form action="" className="member-form">
            <ul>
              <li>
                <input
                  type="text"
                  placeholder="Id"
                  value={userId}
                  onChange={changeUserId}
                />
                {userIdError && (
                  <div className="msg">
                    <small style={{ color: "red", fontSize: "10px" }}>
                      {idMsg}
                    </small>
                  </div>
                )}
              </li>
              <li>
                <input
                  type="password"
                  placeholder="Password"
                  value={pwd}
                  onChange={changePwd}
                />
                {pwdError && (
                  <div className="msg">
                    <small style={{ color: "red", fontSize: "10px" }}>
                      {msgEtc.pwd}
                    </small>
                  </div>
                )}
              </li>
              <li>
                <input
                  type="password"
                  placeholder="Password Check"
                  value={chkPwd}
                  onChange={changeChkPwd}
                />
                {chkPwdError && (
                  <div className="msg">
                    <small style={{ color: "red", fontSize: "10px" }}>
                      {msgEtc.confPwd}
                    </small>
                  </div>
                )}
              </li>
              <li>
                <input
                  type="text"
                  placeholder="Name"
                  value={userName}
                  onChange={changeUserName}
                />
                {
                  //    
                  //  && 
                  userNameError && (
                    <div className="msg">
                      <small style={{ color: "red", fontSize: "10px" }}>
                        {msgEtc.req}
                      </small>
                    </div>
                  )
                }
              </li>
              <li>
                <input
                  type="email"
                  placeholder="Email"
                  value={email}
                  onChange={changeEmail}
                />
                {
                  //    
                  //  && 
                  emailError && (
                    <div className="msg">
                      <small style={{ color: "red", fontSize: "10px" }}>
                        {msgEtc.email}
                      </small>
                    </div>
                  )
                }
              </li>
            </ul>
            <div className="signbtn">
              <button onClick={onSubmit}>
                <p>CREATE</p>
              </button>
            </div>
          </form>
          {/* <div className="forgot-box">
            <p className="forgot">Forgot your password?</p>
          </div> */}
        </div>
      </section>
    </div>
  );
}

export default Member;
