import React, { Fragment, useContext, useRef, useState } from "react";

import { dCon } from "./dCon";
import { Link, useNavigate } from "react-router-dom";
import { initBoardData } from "../func/board_fn";

function MyPost(props) {
  initBoardData();
  const myCon = useContext(dCon);
  let meminfo = JSON.parse(myCon.loginSts);

  const goBdNav = useNavigate();

  // console.log(meminfo.uid);

  const baseData = JSON.parse(localStorage.getItem("board-data"));
  const [pageNum, setPageNum] = useState(1);
  // console.log(baseData);
  let memCartCount = 0;
  baseData.map((v) => {
    if (v.uid == meminfo.uid) {
      memCartCount++;
    }
  });
  // console.log(memCartCount);
  const totalCount = useRef(memCartCount);
  const selRecord = useRef(null);
  const unitSize = 10;

  const bindList = () => {
    // console.log(baseData);

    // 1.    
    let orgData = baseData;

    // 2.   : 
    orgData.sort((a, b) =>
      Number(a.idx) > Number(b.idx) ? -1 : Number(a.idx) < Number(b.idx) ? 1 : 0
    );

    // 3.   
    //  0 9 
    //  10
    //    
    // 1,2,3,4 .....
    //  = (-1) *  
    let sNum = (pageNum - 1) * unitSize;
    //  = (-1) *  
    let eNum = pageNum * unitSize;
    // console.log(":", sNum, "/:", eNum);
    // 
    const myData = [];
    const selData = [];

    orgData.map((v) => {
      if (v.uid == meminfo.uid) {
        myData.push(v);
      }
    });

    // for  
    for (let i = sNum; i < eNum; i++) {
      //     
      if (i >= totalCount.current) break;
      //   
      selData.push(myData[i]);
    } /// for ///

    // console.log(selData);
    let pgCnt = 0;
    return selData.map((v, i) => (
      // v.uid == meminfo.uid &&
      <tr key={i}>
        {/*      */}
        <td>{i + sNum + 1}</td>
        <td>
          {/* <Link
                to="/board"
                onClick={(e) => {
                  // e.preventDefault();
                  //   
                  // setMode("R");
                  //   
                  selRecord.current = v;
                }}
              >
                {v.tit}
              </Link> */}
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              //   
              // setMode("R");
              //   
              // selRecord.current = v;
              goBdNav("/board", { state: { mode: "R", selRcd: v } });
            }}
          >
            {v.tit}
          </a>
        </td>
        <td>{v.unm}</td>
        <td>{v.date}</td>
        <td>{v.cnt}</td>
      </tr>
    ));
  };

  const pagingList = () => {
    //  
    let pagingCount = Math.floor(totalCount.current / unitSize);

    //  
    if (totalCount.current % unitSize > 0) {
      pagingCount++;
    }

    let pgCode = [];
    for (let i = 1; i <= pagingCount; i++) {
      pgCode.push(
        <Fragment key={i}>
          {i == pageNum ? (
            <b style={{ fontWeight: "900" }}>{i}</b>
          ) : (
            //  
            <a
              href="#"
              onClick={(e) => {
                e.preventDefault();
                setPageNum(i);
              }}
            >
              {i}
            </a>
          )}
          {i !== pagingCount && " | "}
        </Fragment>
      );
    } /// for
    return pgCode;
  };
  return (
    // selData.map((v, i) => <div>dd</div>) &&
    <div>
      <h2>My Post</h2>
      <div className="board-area mybd">
        <div className="board-list">
          <table className="mylist-table">
            <thead>
              <tr>
                <th>No</th>
                <th>Title</th>
                <th>Writer</th>
                <th>Date</th>
                <th>Hits</th>
              </tr>
            </thead>
            <tbody>{bindList()}</tbody>
            <tfoot>
              <tr>
                <td className="paging-td">{pagingList()}</td>
              </tr>
            </tfoot>
          </table>
        </div>
      </div>
    </div>
  );
}

export default MyPost;
