import React, { useContext, useEffect, useLayoutEffect, useState } from "react";

import "../../css/login.scss";
import { Link } from "react-router-dom";
import { initData } from "../func/mem_fn";
import { dCon } from "../modules/dCon";

function Login(props) {
  const myCon = useContext(dCon);

  // 1. 
  const [userId, setUserId] = useState("");
  // 2. 
  const [pwd, setPwd] = useState("");

  // 1. 
  const [userIdError, setUserIdError] = useState(false);
  // 2. 
  const [pwdError, setPwdError] = useState(false);
  // [    ] ////
  const msgId = [
    "This is a required entry", //
    "ID does not exist", //  
  ];
  const msgPwd = [
    // 
    "This is a required entry", //
    "Password doesn't match", //  
  ];

  // [3]   :  msgId[0]
  // ->   
  const [idMsg, setIdMsg] = useState(msgId[0]);
  const [pwdMsg, setPwdMsg] = useState(msgId[0]);

  const changeUserId = (e) => {
    let val = e.target.value;

    if (val != "") {
      setUserIdError(false);
    } else {
      setIdMsg(msgId[0]);
      setUserIdError(true);
    }
    setUserId(val);
  };

  // 2.    ///////////
  const changePwd = (e) => {
    //  
    let val = e.target.value;

    // 1.  :
    // 1-1  (false)
    if (val !== "") {
      setPwdError(false);
    }
    // 1-2.  (true)
    else {
      // (1)  ()
      setPwdMsg(msgPwd[0]);
      // (2)  
      setPwdError(true);
    } ///// else/////

    // 4.  
    setPwd(val);
  }; ///////// changePwd  //////////

  const totalValid = () => {
    if(!userId) setUserIdError(true);
    if(!pwd) setPwdError(true);

    if(userId && pwd && !userIdError && !pwdError) return true;
    else return false;
  };

  const onSubmit = (e) => {
    e.preventDefault();

    // console.log("", totalValid());
    if (totalValid()) {
      initData();

      let memData = localStorage.getItem("mem-data");

      memData = JSON.parse(memData);

      let result = memData.find((v) => {
        if (v.uid == userId) return true;
      });
      console.log(result);
      if (!result) {
        // (1)  
        setIdMsg(msgId[1]);
        // (2)  
        setUserIdError(true);
      } ////if///
      else {
        setUserIdError(false);
        if (pwd == result.pwd) {
          sessionStorage.setItem("minfo", JSON.stringify(result));

          // 2.  API   
          myCon.setLoginSts(sessionStorage.getItem("minfo"));
          // ->   minfo   

          // 3.    
          myCon.makeMsg(result.uname);
          console.log(result.uname);

          // 4.     
          document.querySelector(".signbtn p").innerText = " ";

          myCon.goPage("/");
        } else {
          // (1)   
          setPwdMsg(msgPwd[1]);
          setPwdError(true);
        } /// else ////
      }
    } else {
      alert("Change your input");
    } /// else ///
  }
  useEffect(() => {
    //   
    document.querySelector("#user-id").focus();
  }, []);

  return (
    <div id="login-area">
      <section className="login-area">
        <div className="login-wrap">
          <h2>Login</h2>
          <div className="signtxt">
            <p>Don't have an account? &nbsp;</p>
          </div>
          <div className="signtxt">
            <p className="signup">
              <Link to="/member">Sign up here.</Link>
            </p>
          </div>
          <form action="" className="login-form">
            <ul>
              <li>
                <input
                  id="user-id"
                  type="text"
                  placeholder="Your ID"
                  value={userId}
                  onChange={changeUserId}
                />
                {
                  //    
                  //  && 
                  userIdError && (
                    <div className="msg">
                      <small style={{ color: "red", fontSize: "10px" }}>
                        {idMsg}
                      </small>
                    </div>
                  )
                }
              </li>
              <li>
                <input
                  type="password"
                  placeholder="Password"
                  value={pwd}
                  onChange={changePwd}
                />
                {
                  //    
                  //  && 
                  pwdError && (
                    <div className="msg">
                      <small style={{ color: "red", fontSize: "10px" }}>
                        {pwdMsg}
                      </small>
                    </div>
                  )
                }
              </li>
            </ul>
            <div className="signbtn">
              <button onClick={onSubmit}>
                <p>SIGN IN</p>
              </button>
            </div>
          </form>
          {/* <div className="forgot-box">
            <Link>
              <p className="forgot">Forgot your password?</p>
            </Link>
          </div> */}
        </div>
      </section>
    </div>
  );
}

export default Login;
