import React, {
  Fragment,
  useContext,
  useEffect,
  useRef,
  useState,
} from "react";

import { initBoardData } from "../func/board_fn";
import { dCon } from "../modules/dCon";
import $ from "jquery";

import "../../css/board.scss";
import { useLocation } from "react-router-dom";

function Board(props) {
  const myCon = useContext(dCon);

  const sts = myCon.loginSts;

  initBoardData();

  const baseData = JSON.parse(localStorage.getItem("board-data"));
  const [pageNum, setPageNum] = useState(1);
  // [2] 
  const [mode, setMode] = useState("L");

  const loc = useLocation();

  //  //
  // [1]   -    
  const totalCount = useRef(baseData.length);
  // [2]   
  const selRecord = useRef(null);
  // ->       

  const pgPgNum = useRef(1);

  //  
  const unitSize = 10;

  //  
  const pgPgSize = 5;

  // 
  if (loc.state) {
    let mymode = loc.state.mode;
    let selRcd = loc.state.selRcd;
    selRecord.current = selRcd;
    // console.log(selRcd);

    console.log("mymode", mymode);
    console.log("mode", mode);

    if (mymode != mode) {
      setMode(mymode);
      loc.state = null;
    }
  }

  const bindList = () => {
    // console.log(baseData);

    // 1.    
    let orgData = baseData;

    // 2.   : 
    orgData.sort((a, b) =>
      Number(a.idx) > Number(b.idx) ? -1 : Number(a.idx) < Number(b.idx) ? 1 : 0
    );

    //  = (-1) *  
    let sNum = (pageNum - 1) * unitSize;
    //  = (-1) *  
    let eNum = pageNum * unitSize;
    // 
    const selData = [];

    // for  
    for (let i = sNum; i < eNum; i++) {
      //     
      if (i >= totalCount.current) break;
      //   
      selData.push(orgData[i]);
    } /// for ///
    // console.log(selData);

    return selData.map((v, i) => (
      <tr key={i}>
        {/*      */}
        <td>{i + sNum + 1}</td>
        <td>
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              //   
              setMode("R");
              //   
              selRecord.current = v;
            }}
          >
            {v.tit}
          </a>
        </td>
        <td>{v.unm}</td>
        <td>{v.date}</td>
        <td>{v.cnt}</td>
      </tr>
    ));
  }; // bindlist

  const clickButton = (e) => {
    let btnText = e.target.innerText;
    switch (btnText) {
      case "Write":
        setMode("W");

        break;
      case "List":
        setMode("L");
        break;
      case "Submit":
        submitFn();
        break;
      case "Modify":
        setMode("M");
        break;
      case "Delete":
        deleteFn();
        break;
      default:
        break;
    }
  };

  const submitFn = () => {
    let title = $(".write-title").val().trim();

    let cont = $(".write-cont").val().trim();

    if (title == "") {
      alert("Insert title");
      return;
    } else if (cont == "") {
      alert("Insert cont");
      return;
    }

    if (mode == "W") {
      let today = new Date();

      let arrIdx = baseData.map((v) => parseInt(v.idx));

      let maxNum = Math.max(...arrIdx);

      let data = {
        idx: maxNum + 1,
        tit: title,
        cont: cont,
        att: "",
        date: today.toJSON().substr(0, 10),
        uid: JSON.parse(sts).uid,
        unm: JSON.parse(sts).uname,
        cnt: "0",
      };
      console.log(data);

      let locals = localStorage.getItem("board-data");
      locals = JSON.parse(locals);
      locals.push(data);
      localStorage.setItem("board-data", JSON.stringify(locals));

      totalCount.current = baseData.length;

      setMode("L");
    } else if (mode == "M") {
      let today = new Date();
      //   idx
      let currIdx = selRecord.current.idx;

      baseData.find((v) => {
        // console.log(v,selRecord);
        if (v.idx == currIdx) {
          // [   ]
          //  : tit, cont
          //   selRecord   idx
          //      !

          // (1)  : tit
          v.tit = title;
          // (2)  : cont
          v.cont = cont;
          // 
          // (  DB  
          //      
          //   !)
          // (3)  : mdate
          v.mdate = today.toJSON().substr(0, 10);

          //   !
          return true;
        } /// if ///
      }); /////// find  /////////
      localStorage.setItem("board-data", JSON.stringify(baseData));
      setMode("L");
    }
  }; //submitFn

  const deleteFn = () => {
    // 
    if (window.confirm("Are you sure you want to delete?")) {
      // 1.  idx
      let currIdx = selRecord.current.idx;
      // 2. some()   
      // find()  some()  boolean
      //  !   !
      baseData.some((v, i) => {
        if (v.idx == currIdx) {
          //   !
          //   splice(,1)
          baseData.splice(i, 1);

          // true  !
          return true;
        } ///// if ////
      }); ///// some ///////

      // 3.   //////
      localStorage.setItem("board-data", JSON.stringify(baseData));

      // 4.      
      //       !
      totalCount.current = baseData.length;

      // 4.   ->  /////
      // -> ! "L"
      setMode("L");
      // ->   !
      setPageNum(1);
    } ///////// if ///////////////
  }; //////// deleteFn ///////////////

  //  
  return (
    <div className="board-area">
      <h2>Question</h2>
      {mode == "L" && (
        <ListMode
          bindList={bindList}
          pageNum={pageNum}
          setPageNum={setPageNum}
          totalCount={totalCount}
          unitSize={unitSize}
          pgPgNum={pgPgNum}
          pgPgSize={pgPgSize}
        />
      )}
      {mode == "W" && <WriteMode sts={JSON.parse(sts)} />}
      {mode == "R" && <ReadMode selRecord={selRecord} sts={sts} />}
      {mode == "M" && <ModifyMode selRecord={selRecord} />}
      <table className="boardBtn">
        <tbody>
          <tr>
            <td>
              {mode == "L" && sts && (
                <button onClick={clickButton}>Write</button>
              )}
              {mode == "W" && (
                <>
                  <button onClick={clickButton}>List</button>
                  <button onClick={clickButton}>Submit</button>
                </>
              )}
              <>
                {mode == "R" &&
                  sts &&
                  JSON.parse(sts).uid == selRecord.current.uid && (
                    <>
                      <button onClick={clickButton}>Modify</button>
                      <button onClick={clickButton}>Delete</button>
                    </>
                  )}
                {mode == "R" && <button onClick={clickButton}>List</button>}
              </>

              {mode == "M" && (
                <>
                  <button onClick={clickButton}>Submit</button>
                  <button onClick={clickButton}>List</button>
                </>
              )}
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  );
}
const ListMode = ({
  bindList,
  pagingList,
  pageNum,
  setPageNum,
  totalCount,
  unitSize,
  pgPgNum,
  pgPgSize,
}) => {
  return (
    <div className="board-list">
      <table className="list-table">
        <colgroup>
          <col span="1" style={{ width: "1%" }} />
          <col span="1" style={{ width: "40%" }} />
          <col span="1" style={{ width: "20%" }} />
          <col span="1" style={{ width: "20%" }} />
          <col span="1" style={{ width: "20%" }} />
        </colgroup>
        <thead>
          <tr>
            <th>No</th>
            <th>Title</th>
            <th>Writer</th>
            <th>Date</th>
            <th>Hits</th>
          </tr>
        </thead>
        <tbody>{bindList()}</tbody>
        <tfoot>
          <tr>
            <td className="paging-td">
              {
                <PagingList
                  pageNum={pageNum}
                  setPageNum={setPageNum}
                  totalCount={totalCount}
                  unitSize={unitSize}
                  pgPgNum={pgPgNum}
                  pgPgSize={pgPgSize}
                />
              }
            </td>
          </tr>
        </tfoot>
      </table>
    </div>
  );
};

const WriteMode = ({ sts }) => {
  useEffect(() => {
    document.querySelector(".write-title").focus();
  }, []);
  return (
    <table className="other-table">
      <tbody>
        <tr>
          <td>Name</td>
          <td>
            <input type="text" defaultValue={sts.uname} readOnly />
          </td>
        </tr>
        <tr>
          <td>Email</td>
          <td>
            <input type="text" defaultValue={sts.email} readOnly />
          </td>
        </tr>
        <tr>
          <td>Title</td>
          <td>
            <input type="text" className="write-title" />
          </td>
        </tr>
        <tr>
          <td>Content</td>
          <td>
            <textarea
              className="write-cont"
              cols="60"
              maxLength="300"
              rows="10"
            ></textarea>
          </td>
        </tr>
        <tr>
          <td>Attachment</td>
          <td></td>
        </tr>
      </tbody>
    </table>
  );
};

const ReadMode = ({ selRecord, sts }) => {
  const data = selRecord.current;
  if (!sessionStorage.getItem("board-rec")) {
    sessionStorage.setItem("board-rec", "[]");
  }

  let rec = JSON.parse(sessionStorage.getItem("board-rec"));

  let isRec = rec.includes(data.idx);
  console.log(isRec);

  if (sts) {
    console.log(data.uid, JSON.parse(sts).uid);
    if (data.uid == JSON.parse(sts).uid) {
      isRec = true;
    }
  }
  if (!isRec) rec.push(data.idx);

  sessionStorage.setItem("board-rec", JSON.stringify(rec));

  if (!isRec) {
    let bdData = JSON.parse(localStorage.getItem("board-data"));
    bdData.some((v) => {
      if (v.idx == data.idx) {
        v.cnt = Number(v.cnt) + 1;
        return true;
      }
    });
    localStorage.setItem("board-data", JSON.stringify(bdData));
  }

  return (
    <>
      <table className="other-table">
        <tbody>
          <tr>
            <td>Name</td>
            <td>
              <input type="text" defaultValue={data.unm} readOnly />
            </td>
          </tr>
          <tr>
            <td>Title</td>
            <td>
              <input
                type="text"
                className="write-title"
                defaultValue={data.tit}
                readOnly
              />
            </td>
          </tr>
          <tr>
            <td>Content</td>
            <td>
              <textarea
                className="write-cont"
                cols="60"
                maxLength="300"
                rows="10"
                defaultValue={data.cont}
                readOnly
              ></textarea>
            </td>
          </tr>
          <tr>
            <td>Attachment</td>
            <td></td>
          </tr>
        </tbody>
      </table>
    </>
  );
};

const ModifyMode = ({ selRecord }) => {
  useEffect(() => {
    document.querySelector(".write-title").focus();
  }, []);
  const data = selRecord.current;
  return (
    <table className="other-table">
      <tbody>
        <tr>
          <td>Name</td>
          <td>
            <input type="text" defaultValue={data.unm} readOnly />
          </td>
        </tr>
        <tr>
          <td>Title</td>
          <td>
            <input
              type="text"
              className="write-title"
              defaultValue={data.tit}
            />
          </td>
        </tr>
        <tr>
          <td>Content</td>
          <td>
            <textarea
              className="write-cont"
              cols="60"
              maxLength="300"
              rows="10"
              defaultValue={data.cont}
            ></textarea>
          </td>
        </tr>
        <tr>
          <td>Attachment</td>
          <td></td>
        </tr>
      </tbody>
    </table>
  );
};

export default Board;

const PagingList = ({
  totalCount,
  unitSize,
  pageNum,
  setPageNum,
  pgPgNum,
  pgPgSize,
}) => {
  //  
  let pagingCount = Math.floor(totalCount.current / unitSize);

  //  
  if (totalCount.current % unitSize > 0) {
    pagingCount++;
  }

  let pgPgCount = Math.floor(pagingCount / pgPgSize);

  if (pagingCount % pgPgSize > 0) {
    pgPgCount++;
  }

  let initNum = (pgPgNum.current - 1) * pgPgSize;

  let limitNum = pgPgNum.current * pgPgSize;

  let pgCode = [];

  for (let i = initNum; i < limitNum; i++) {
    if (i >= pagingCount) break;

    pgCode.push(
      <Fragment key={i}>
        {i + 1 == pageNum ? (
          <b style={{ fontWeight: "900" }}>{i + 1}</b>
        ) : (
          //  
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              setPageNum(i + 1);
            }}
          >
            {i + 1}
          </a>
        )}
        {i + 1 !== limitNum && i + 1 < pagingCount && " | "}
      </Fragment>
    );
  } /// for

  {
    // [3]    
    // :    
    //   push()
    pgCode.push(
      pgPgNum.current === pgPgCount ? (
        ""
      ) : (
        // for     key  ,  
        //    
        <Fragment key={-2}>
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              goPaging(1, true);
            }}
            title="move next"
            className="next"
          >
            
          </a>
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              goPaging(1, false);
            }}
            title="move next"
            className="nextFst"
          >
            
          </a>
        </Fragment>
      )
    );
  }
  {
    // [3]    
    // :    
    //   push()
    pgCode.unshift(
      pgPgNum.current === 1 ? (
        ""
      ) : (
        // for     key  ,  
        //    
        <Fragment key={-1}>
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              goPaging(-1, false);
            }}
            title="move previous"
            className="previous"
          >
            
          </a>
          <a
            href="#"
            onClick={(e) => {
              e.preventDefault();
              goPaging(-1, true);
            }}
            title="move previous end"
            className="previousEnd"
          >
            
          </a>
        </Fragment>
      )
    );
  }

  // [] //
  const goPaging = (dir, opt) => {
    // dir - (:+1, :-1)
    // opt - (true), (false)
    console.log("", dir, "/:", opt);

    //   
    let newPgPgNum;
    // 1. opt     
    // (1)     
    if (opt) {
      newPgPgNum = pgPgNum.current + dir;
    }
    // (2)  
    //      (pgPgCount)
    // (-1)     (1)
    else newPgPgNum = dir == 1 ? pgPgCount : 1;

    //    
    pgPgNum.current = newPgPgNum;

    // 3.       
    // ->    +1   
    //   
    let landingPage = (pgPgNum.current - 1) * pgPgSize + 1;
    console.log(":", landingPage);
    //     
    setPageNum(landingPage);
  };

  return pgCode;
}; //paging
